  methods: {
    peopleMove() {
      let top = this.$refs.teamItem1.getBoundingClientRect().top;
      let glap = top - clientHeight;
      // console.log("glap",glap)
      if (glap < 0 && !this.isShow1) {
        this.isShow1 = true;
      }


      let top2 = this.$refs.teamItem2.getBoundingClientRect().top;
